* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Josefin Sans", sans-serif;
}

img {
  width: 100%;
  display: block;
}

li {
  list-style-type: none;
}

.logo {
  width: 150px;
  z-index: 3;
}

.heading {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 30px;
}

@media (min-width: 768px) {
  .heading {
    text-align: left;
    font-size: 40px;
  }
}
.hover:hover {
  transform: scale(1.1);
  box-shadow: inset 0 0 0 568px rgba(217, 228, 250, 0.7);
  z-index: -1;
}

.hover:hover > .item-text {
  color: hsl(0deg, 0%, 0%);
  padding: 10px;
  font-weight: 700;
}

.unders-score {
  position: relative;
}
.unders-score::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 5px;
  position: absolute;
  left: 16px;
  bottom: -4px;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.25s ease;
}
.unders-score:hover::after {
  transform: scaleX(1);
}

.unders-score--social {
  position: relative;
}
.unders-score--social::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 5px;
  position: absolute;
  left: 0;
  bottom: -6px;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.25s ease;
}
.unders-score--social:hover::after {
  transform: scaleX(1);
}

.hero {
  background-image: url(../img/mobile/image-hero.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 40.625rem;
  padding: 2.5rem 1.25rem;
  position: relative;
}
.hero__description {
  margin: auto;
  margin-top: 50%;
  border: 0.125rem solid white;
  padding: 1.875rem;
  width: 97%;
  max-width: 24.6875rem;
}
.hero__description--text {
  font-size: 2.5rem;
  color: white;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .hero {
    background-image: url(../img/desktop/image-hero.jpg);
    height: 100vh;
    padding: 2.5rem 6.25rem;
  }
  .hero__description {
    margin: 0;
    max-width: 32.5rem;
    position: absolute;
    top: 40%;
  }
  .hero__description--text {
    font-size: 3.75rem;
    color: white;
    text-transform: uppercase;
  }
}
.nav {
  --state-close:scale(0);
  --state-hamburger:scale(1);
  --state-menu:translateX(-100%);
  display: grid;
  justify-content: space-between;
  align-items: center;
  grid-template-columns: repeat(2, max-content);
}
.nav:target {
  --state-close:scale(1);
  --state-hamburger:scale(0);
  --state-menu:translateX(0);
}

.nav__close,
.nav__hamburger {
  z-index: 2;
  cursor: pointer;
  grid-column: -2/-1;
  grid-row: 1/2;
  transition: transform 0.4s;
}

.nav__hamburger {
  transform: var(--state-hamburger);
}

.nav__close {
  transform: var(--state-close);
}

.nav__links {
  z-index: 1;
  align-content: center;
  background-color: hsl(0deg, 0%, 0%);
  inset: 0;
  display: grid;
  gap: 1.25rem;
  padding: 1.25rem;
  position: absolute;
  transform: var(--state-menu);
  transition: transform 0.4s linear;
  width: 100%;
  font-size: 1.5rem;
  text-transform: uppercase;
  height: 40.625rem;
}

.nav__link {
  text-decoration: none;
  color: white;
}

@media (min-width: 768px) {
  .nav {
    align-items: unset;
    height: 1.875rem;
    --state-hamburger:scale(0);
    --state-menu:translateX(1);
  }
  .nav:target {
    --state-close:scale(0);
  }
  .nav__links {
    align-content: unset;
    padding: 0;
    background-color: unset;
    gap: 20px;
    position: unset;
    transform: unset;
    grid-auto-flow: column;
    grid-column: -2/-1;
    grid-row: 1/2;
    height: 30px;
    font-size: 18px;
    text-transform: capitalize;
  }
}
.main {
  padding: 0 1.25rem;
  margin-top: 6.25rem;
}
.main__image {
  width: 100%;
}
.main__description {
  margin-top: 3.125rem;
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
}
.main__text {
  margin-top: 1.25rem;
  font-weight: 700;
  color: hsl(0deg, 0%, 55%);
  line-height: 1.5;
}

@media (min-width: 768px) {
  .main {
    padding-inline: 8.75rem;
  }
  .main__image {
    display: none;
  }
  .main__loader-interative {
    background-image: url(../img/desktop/image-interactive.jpg);
    background-size: 46.875rem;
    background-repeat: no-repeat;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 12.5rem 16.875rem;
  }
  .main__description {
    grid-row: 2/3;
    grid-column: 2/3;
    padding: 5rem 0 1.875rem 6.25rem;
    background-color: hsl(0deg, 0%, 100%);
    margin: 0;
    max-width: 37.5rem;
  }
  .main__text {
    margin-top: 0.625rem;
    font-weight: 700;
    color: hsl(0deg, 0%, 55%);
    line-height: 1.5;
    font-size: 1rem;
    text-align: left;
  }
}
.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.creations {
  margin-top: 6.25rem;
  text-align: center;
}
.creations__grid {
  margin-top: 3.125rem;
  display: grid;
  grid-template-rows: repeat(8, 130px);
  object-fit: cover;
  gap: 1.25rem;
}

@media (min-width: 550px) {
  .creations {
    margin-top: 9.375rem;
  }
  .creations__grid {
    grid-template-rows: repeat(4, 360px);
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 850px) {
  .creations {
    margin-top: 9.375rem;
  }
  .creations__grid {
    grid-template-rows: repeat(2, 400px);
    grid-template-columns: repeat(4, 1fr);
  }
}
