.mask {
  overflow: hidden;
  position: relative;
}

.item-text {
  margin-top: 20%;
  color: hsl(0deg, 0%, 100%);
  text-transform: uppercase;
  font-size: 1.25rem;
  font-weight: 400;
  text-align: left;
  position: absolute;
  bottom: 1.25rem;
  transition: color 0.3s, padding 0.3s;
}

.br {
  display: block;
}

.see-all {
  display: inline-block;
  margin-top: 30px;
  text-decoration: none;
  padding: 10px 50px;
  border: 1px solid hsl(0deg, 0%, 0%);
  color: hsl(0deg, 0%, 0%);
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 14px;
  font-weight: 700;
  transition: background-color 0.6s, color 0.6s;
  border-radius: 2px;
}
.see-all:hover {
  background-color: hsl(0deg, 0%, 0%);
  color: hsl(0deg, 0%, 100%);
}

#see-all--desktop {
  display: none;
}

@media (min-width: 768px) {
  #see-all--desktop {
    display: block;
  }
  .see-all {
    margin-top: 0;
    padding: 10px 30px;
  }
  #see-all {
    display: none;
  }
}
.footer {
  margin-top: 6.25rem;
  text-align: center;
  background-color: hsl(0deg, 0%, 0%);
  padding-block: 3.75rem 1.25rem;
}
.footer__logo {
  width: 9.375rem;
  margin: 0 auto;
}
.footer__nav--links {
  margin-top: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
.footer__nav--link {
  text-decoration: none;
  text-transform: capitalize;
  color: hsl(0deg, 0%, 100%);
  font-size: 1.125rem;
}

.social-media {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  gap: 0.625rem;
}
.social-media__link {
  cursor: pointer;
}
.social-media__link::before {
  content: "";
  padding: 0.75rem;
  display: inline-block;
}
.social-media__link[href*="facebook.com"]::before {
  background-image: url(../img/icon-facebook.svg);
  background-position: center;
}
.social-media__link[href*="twitter.com"]::before {
  background-image: url(../img/icon-twitter.svg);
  background-position: center;
  background-repeat: no-repeat;
}
.social-media__link[href*="pinterest.com"]::before {
  background-image: url(../img/icon-pinterest.svg);
  background-position: center;
}
.social-media__link[href*="instagram.com"]::before {
  background-image: url(../img/icon-instagram.svg);
  background-position: center;
}
.social-media__accecible {
  width: 0.0625rem;
  height: 0.0625rem;
  padding: 0;
  margin: -0.0625rem;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  display: block;
}

.corpy-right {
  margin-top: 1.25rem;
  color: hsl(0deg, 0%, 55%);
  font-size: 1.125rem;
}

@media (min-width: 768px) {
  .footer {
    padding: 3.125rem 8.75rem;
    display: flex;
    justify-content: space-between;
  }
  .footer__logo {
    margin: 0;
  }
  .footer__nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__nav--links {
    flex-direction: row;
    margin-top: 1.25rem;
  }
  .social-media {
    margin: 0;
    justify-content: flex-end;
  }
}